<template>
	<v-btn
		depressed
		color="cyan"
		@click="exportWord"
		dark>
		<v-icon class="mr-2">mdi-file-word</v-icon>
		export to word
	</v-btn>
</template>

<script>
import { saveAs } from 'file-saver';
import { 
	Document, Packer, 
	Paragraph, TextRun, 
	AlignmentType,
} from "docx";

  export default{
    // data: () => ({
    //   state: {
    //     name: 'San Luis Potosi',
    //     map: 'data:image/png;base64',
    //     municipalities: [
    //       {name:'San Luis Potosi', population: 824000}, 
    //       {name:'Rio Verde', population: 160000},
    //       {name:'Cd Valles', population: 176000},
    //       {name:'Matehuala', population:82726}
    //     ],
    //     tourist_attractions: [
    //       'Tamtoc', 'Stano de las Golondrinas', 'Cascada de Tamul' 
    //     ]
    //   }
    // }),

    methods: {
      exportWord() {
				const doc = new Document();

				doc.addSection({
					children: [
						new Paragraph({
							text: 'LOG PENDAMPINGAN',
							alignment: AlignmentType.CENTER
						}),
						new Paragraph({
							children: [
								new TextRun(`NAMA TENANT\t: Smartedu`).break(),
								new TextRun(`CEO\t\t\t: Mr. X`).break(),
								new TextRun(`SPRINT\t\t: 2`).break(),
								new TextRun(`TANGGAL\t\t: 20 November 2020`).break(),
							]
						}),
						
					]
				})

				// Used to export the file into a .docx file
				Packer.toBlob(doc).then(blob => {
          saveAs(blob, "detailed_report.docx");
				});

      }
    }
  }
</script>